import React, { Component } from 'react';
import { Radio } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { fromJS, is } from 'immutable';

class Xradio extends Component {
	state = {
		defaultOptions: {
			mode: 'text/html',
			theme: 'material',
			lineNumbers: true,
			tabSize: 2
		},
		optionsjs: {
			mode: 'javascript',
			theme: 'material',
			lineNumbers: true,
			tabSize: 2
		},
		value: '1'
	}

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}

	onChange = (value) => {
		this.setState({
			value: value
		})
	}

	render() {
		return (
			<div id="x-radio">
				<h3>Radio 单选框</h3>
				<div className="mt-20 mb-20">
					<Radio value="1" checked={this.state.value == 1} onChange={this.onChange}>备选项</Radio>
					<Radio value="2" checked={this.state.value == 2} onChange={this.onChange}>备选项</Radio>
				</div>
				<Codemirror value={
					`<Radio value="1" checked={this.state.value == 1} onChange={this.onChange}>备选项</Radio>
<Radio value="2" checked={this.state.value == 2} onChange={this.onChange}>备选项</Radio>`
				} options={this.state.optionsjs} />

			</div>
		);
	}
}

export default Xradio;
